.wp-block-imath-bout-de-code {

	.gist {

		.blob-num {
			border: none;
		}
	}

	&.use-dark-mode {

		.gist {

			.highlight {
				background: #002240;
			}

			.blob-num,
			.blob-code-inner,
			.pl-en {
				color: #fff;
			}

			.pl-mb,
			.pl-mh .pl-en {
				font-weight: 700;
			}

			.pl-c,
			.pl-c span,
			.pl-mi {
				font-style: italic;
			}

			.pl-ent,
			.pl-v {
				color: #fd0;
			}

			.pl-mh,
			.pl-mh .pl-en,
			.pl-sr .pl-cce {
				color: #eb939a;
			}

			.pl-s1 .pl-s2,
			.pl-smi,
			.pl-smp,
			.pl-stj,
			.pl-vo,
			.pl-vpf {
				color: #ccc;
			}

			.pl-pds,
			.pl-s,
			.pl-s1,
			.pl-s1 .pl-pse .pl-s2,
			.pl-s1 .pl-v {
				color: #3ad900;
			}

			.pl-s3,
			.pl-sc {
				color: #ffb054;
			}

			.pl-sr,
			.pl-sr .pl-sra,
			.pl-sr .pl-sre,
			.pl-src {
				color: #80ffc2;
			}

			.pl-mdht,
			.pl-mi1 {
				color: #f8f8f8;
				background: rgba(0, 64, 0, 0.5);
			}

			.pl-id,
			.pl-ii,
			.pl-md,
			.pl-mdhf {
				color: #f8f8f8;
				background: #800f00;
			}

			.pl-e,
			.pl-k,
			.pl-mdh,
			.pl-mdr,
			.pl-ml,
			.pl-mm,
			.pl-mo,
			.pl-mp,
			.pl-mr,
			.pl-ms,
			.pl-st,
			.pl-mi {
				color: #ff9d00;
			}

			.highlight-source-js .pl-st {
				color: #ffee80;
			}

			.highlight-source-css .pl-s3 {
				color: #80ffbb;
			}

			.highlight-text-html-basic .pl-ent {
				color: #9effff;
			}

			.pl-c,
			.pl-c span,
			.pl-mq {
				color: #08f;
			}

			.pl-c1,
			.pl-sv,
			.pl-mb {
				color: #ff628c;
			}
		}
	}
}
